body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

ol, ul, li {
  list-style: none;
  margin: 0;
  padding: 0;
}
.slidebox, .slidebox ul, .slidebox ul li{
  width: 100%;
  height: 500px;
  min-width: 980px;
  position: absolute;
  top: 0;
  left: 0;
}
.slidebox ul li:nth-child(1) {
  background: url(../../static/media/1.8bc08be9.jpg) top center no-repeat;
  /* background-size: cover; */
}
.slidebox ul li:nth-child(2) {
  background: url(../../static/media/2.aa09190e.jpg) top center no-repeat;
  /* background-size: cover; */
}
.slidebox ul li:nth-child(3) {
  background: url(../../static/media/3.2827e02b.jpg) top center no-repeat;
  /* background-size: cover; */
}
.slidebox ul li:nth-child(4) {
  background: url(../../static/media/4.b029e4ef.jpg) top center no-repeat;
  /* background-size: cover; */
}

.slidebox ol {
  width: 100%;
  height: 30px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 6;
}
.slidebox ol li {
  width: 10px;
  height: 10px;
  border-radius: 5px;
  margin: 0 10px;
  cursor: pointer;
}
.active {
  z-index: 1;
  -webkit-animation: opt 0.2s ease-in;
          animation: opt 0.2s ease-in;
}
.red {
  background-color: #ff6460;
}
.white {
  background-color: #fff;
}
@-webkit-keyframes opt {
  0% {
    opacity: .9;
  }
  100% {
    opacity: 1;
  }
}


.banner {
  width: 980px;
  height: 175px;
  display: flex;
  justify-content: center;
  margin: 0 auto;
}
.banner_li {
  width: 25%;
  position: relative;
}
.li_bg {
  width: 150px;
  height: 175px;
  margin: 0 auto;
  background-size: 100% 100%;
  -webkit-transform: rotateZ(0);
          transform: rotateZ(0);
  -webkit-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
  transition: all .3s ease-out;
}
.li_bg.active {
  -webkit-transform: rotateZ(90deg);
          transform: rotateZ(90deg);
}
.li_title {
  font-size: 18px;
  color: #fff;
  font-weight: bold;
  margin-bottom: 10px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-180%);
          transform: translate(-50%,-180%)
}
.li_img {
  display: block;
  width: 80px;
  height: 65px;
  -webkit-transform: rotate(0);
          transform: rotate(0);
  -webkit-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
  transition: all .3s ease-out;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-20%);
          transform: translate(-50%,-20%)
}
.li_img.act {
  -webkit-transform: translate(-30%,5%) rotate(-45deg);
          transform: translate(-30%,5%) rotate(-45deg);
  width: 120px;
  height: 100px;
}

.query_box {
  width: 320px;
  background-color: rgba(255,255,255,.32);
  position: absolute;
  left: 50%;
  margin-left: -490px;
  bottom: 30px;
  z-index: 66;
  padding: 10px 0;
}
.query {
  width: 300px;
  background-color: #fff;
  margin: 0 auto;
  padding-bottom: 20px;
}
.icon_box {
  height: 30px;
  color: #0085ff;
  font-size: 20px;
  text-align: left;
  text-indent: 15px;
  padding-top: 10px;
}
.iconfont {
  margin-right: 10px;
  font-size: 20px;
}
.textarea {
  width: 280px;
  min-height: 30px;
  border-radius: 5px;
  font-size: 16px;
  outline: none;
  resize: none;
  border-color:#aaa;
  text-indent: 8px;
  transition: all .3s ease-in;
  padding-top: 5px;
}
.textarea::-webkit-input-placeholder {
  font-size: 12px;
  line-height: 25px;
}
.textarea::-ms-input-placeholder {
  font-size: 12px;
  line-height: 25px;
}
.textarea::placeholder {
  font-size: 12px;
  line-height: 25px;
}
.btn {
  text-align: right;
  margin-right: 10px;
  margin-top: 5px;
  height: 0px;
  overflow: hidden;
  transition: all .3s ease-in;
}
.btn button {
  border: none;
  background: #d0d0d0;
  color: #b9b9b9;
  outline: none;
  padding: 10px 15px;
  border-radius: 5px;
